import React from "react";
import Button from '@material-ui/core/Button';
import CheckIcon from '@material-ui/icons/Check';
import Chip from '@material-ui/core/Chip';
import CloseIcon from '@material-ui/icons/Close';
import ArrowForwardOutlinedIcon from '@material-ui/icons/ArrowForwardOutlined';

import "./ProductCard.css";

export const ProductCard = ({condition, currency, img, name, price, shipping, stock, total_price}) => {

    return(
        <div className="card mb-3 max-width overflow-hidden">
            <div className="row no-gutters">

                <div className="col-12 col-sm-4 col-md-4 col-lg-3 col-xl-3 d-flex justify-content-center align-items-center">
                    <img 
                        src={img} 
                        className="card-img"
                        alt="product"
                    />
                </div>

                <div className="col-12 col-sm-8 col-md-8 col-lg-6 col-xl-7">
                    <div className="card-body">
                        <div>
                            <h6 className="card-title">
                                {name}
                            </h6>
                            <hr/>
                        </div>
                        <h2 className="card-text">
                            <small>
                                {currency}
                            </small>
                            &nbsp;{price}
                        </h2>
                        <small>
                            Pagalo en
                            <span className="green">
                                <b>
                                    {` 6 cuotas de $${Math.round(total_price/6)}`}
                                </b>
                            </span>
                        </small>
                        <hr/>
                        <div className="row">
                            <div className="col-md-12">
                                <p className="card-text">
                                    {stock <= 0
                                    ? <b className= "red">
                                        <CloseIcon 
                                            fontSize= "small"
                                        />
                                        &nbsp;
                                        Fuera de stock
                                    </b>
                                    : <b className= "green">
                                        <CheckIcon 
                                            fontSize= "small"
                                        />
                                        &nbsp;
                                        En stock
                                    </b>
                                    }
                                    <br/>
                                    {stock === 1
                                    ? <small style={{color: "red"}}>&nbsp;¡Último disponible!</small>
                                    : <small>{`${stock} disponibles`}</small>
                                    }   
                                </p>                        
                            </div>
                        </div>
                    </div>
                </div>
                
                <div className= "d-none d-md-none d-sm-none d-lg-flex col-lg-3 col-xl-2 flex-column align-items-center">
                    <Chip
                        label={condition}
                        color="primary"
                        className="chip"
                        size="small"
                    />                    
                    {shipping !== null
                    ?<Chip
                        label={shipping}
                        color="secondary"
                        className="chip"
                        size="small"
                    />
                    : null
                    }
                </div>

            </div>
        </div>

    );
};
